/* https://developer.helpscout.com/seed/glossary/itcss/#the-triangle */
@layer settings, tools, generic, elements, objects, typography, components, overrides;
@layer settings {
  :root {
    --clr-light: #fdffc4;
    --clr-dark: #00332a;
    --clr-primary: #dbffa2;
    --clr-secondary: #c3fcf2;
    --clr-accent: #ff7750;
    --foreground: var(--clr-dark);
    --background: var(--clr-light);
    --font-inter: "Inter", sans-serif;
  }

  .darkmode {
    --clr-light: #fdffc4;
    --clr-dark: #00332a;
    --clr-primary: #202302;
    --clr-secondary: #00100d;
    --clr-accent: #ff7750;
    --foreground: var(--clr-light);
    --background: var(--clr-dark);
  }
}
/*
Example:

h1 {
  @include breakpoint(large) {
  font-size: 3em
  }
}

- loads breakpoint in h1 tag, 
- translates to CSS...

@media (min-width: 87.5em) {
  h1 {
    font-size: 3rem;
  }
}

*/
/**

h1 {
  font-size: rem(30)
}

results in...

h1 {
  font-size: 1.875rem
}
*/
@layer generic {
  html {
    font-size: 100%;
    box-sizing: border-box;
  }

  *, *::before, ::after {
    box-sizing: inherit;
  }

  body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
    background: var(--background);
    color: var(--foreground);
  }
}
@layer elements {}
@objects {}
@typography {
  :root h1,
:root h2,
:root h3 {
    margin-top: 0;
  }
  :root a,
:root a:visited,
:root a:active {
    text-decoration: none;
  }
}
@layer components {
  .card {
    padding: 1.25rem;
  }
}
@layer components {
  .navbar {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    color: white;
  }

  .navbar__title {
    font-size: 1.5rem;
    margin: 0.5rem;
  }

  .navbar__links {
    height: 100%;
  }

  .navbar__links-list {
    margin: 0;
    padding: 0;
    display: flex;
  }

  .navbar__links-item {
    list-style: none;
  }

  .navbar__link {
    text-decoration: none;
    color: white;
    padding: 1rem;
    display: block;
  }

  .navbar__link:hover {
    background-color: #555;
  }

  .navbar__toggle-button {
    position: absolute;
    top: 0.75rem;
    right: 1rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 21px;
  }

  .navbar__toggle-button-bar {
    height: 3px;
    width: 100%;
    background-color: white;
    border-radius: 10px;
  }

  @media (max-width: 800px) {
    .navbar__toggle-button {
      display: flex;
    }

    .navbar__links {
      display: none;
      width: 100%;
    }

    .navbar {
      flex-direction: column;
      align-items: flex-start;
    }

    .navbar__links-list {
      width: 100%;
      flex-direction: column;
    }

    .navbar__links-list .navbar__links-item {
      text-align: center;
    }

    .navbar__links-list .navbar__links-item.navbar__dark-mode-toggle {
      text-align: center;
    }

    .navbar__link {
      padding: 0.5rem 1rem;
    }

    .navbar__links.navbar__links--active {
      display: flex;
    }
  }
}
@layer components {
  .navbar__dark-mode-toggle {
    top: 1em;
    right: 1em;
    color: var(--foreground);
    border: 2px solid currentColor;
    padding: 4px;
    margin: 0.5rem;
    background: transparent;
    cursor: pointer;
    border-radius: 5px;
    color: white;
    width: 30px;
    height: 30px;
  }
}
@layer components {
  .player {
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 50;
  }

  .player__wrapper {
    border: 1px solid transparent;
    border-radius: 10px;
    background-color: #ddd;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
  }

  .player__details-slider {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    width: 100%;
    max-width: 100%;
    gap: 10px;
  }

  .player__details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
  }

  .player__track-name {
    font-size: 1.5rem;
  }

  .player__buttons {
    flex-shrink: 0;
    margin-right: 20px;
  }

  .active {
    color: black;
  }

  .playpause-track {
    padding: 25px;
    opacity: 0.8;
    transition: opacity 0.2s;
  }

  .playpause-track:hover {
    opacity: 1;
  }

  .player__slider_container {
    flex-grow: 1;
    width: 100%;
  }

  .player__seek_slider, .player__volume_slider {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100% !important;
    height: 5px;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
    background: #83A9FF;
    -webkit-transition: 0.2s;
    transition: opacity 0.2s;
  }

  .player__seek_slider::-webkit-slider-thumb,
.player__volume_slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 15px;
    height: 15px;
    background: white;
    border: 3px solid #3774FF;
    cursor: grab;
    border-radius: 100%;
  }

  .player__seek_slider:hover,
.player__volume_slider:hover {
    opacity: 1;
  }

  .player__seek_slider {
    width: 60%;
  }

  .player__current-time {
    font-size: 1rem;
  }

  i,
i.fa-play-circle,
i.fa-pause-circle,
p {
    cursor: pointer;
  }
}
@overrides {}
@layer settings, tools, generic, elements, objects, typography, components, overrides;